body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.heading-background {
    /* background-color: #34495E;   */
    height: 55rem;
    background: url(/v3_personal_portfolio/static/media/rain.5410f47b.jpg);
}

.subSection {
    height: 18rem;
    background: url(/v3_personal_portfolio/static/media/rain.5410f47b.jpg);

}

.margin-top {
    margin-top: 16rem !important;
}

.heading-button {
    border-color: white;
    background: none;
    color:white;
}

.heading-font {
    text-shadow: 1px 1px darkgray;
}

.about-background {
    height: 60rem;
    border-bottom: 1px solid lightgray;
}

.portfolio-bg {
    height: 56rem;
}

.contact-bg {
    height: 57rem;
}

.img-self {
    height: 30rem;
    position: absolute;
    z-index:9999;
    box-shadow: 10px 18px 10px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

}

.img-blur {
    height: 30rem;
    position: absolute; 
    top: 90px;
    left: 150px;
    z-index: -8888;
}

.row-size {
    height: 50rem;
}

.img-moreBlur {
    height: 30rem;
    position: relative;
    top: 150px;
    left: 40px;
    z-index: -9999;


}

.card-column {
    border-bottom: none !important;
}
.projects {

    box-shadow: 10px 10px 10px 0 rgba(0, 0, 0, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.19);
    border: none;

}

.showDescription {
    display: none;
}

.projects:hover .showDescription{
    display: block;
    z-index: 2;
    background-color: gray;
    opacity: 0.8;
}
.contact-margin {
    margin-bottom: 12rem;
}
.img-logo {
    width: 2.2rem;
    opacity: 0.6;
}

.navfont {
    opacity: 0.6;
    color: white !important;
}
